
.tasteRegister {
  padding: 12px 12px 50px 12px;
  background: #fff;
}
.tasteRegister h1 {
  font-size: 18px;
  line-height: 1.3;
}
.tasteRegister .info {
  margin: 8px 0 15px 0;
}
.tasteRegister .rules {
  margin: 25px 5px 25px 0;
  padding: 10px 10px 10px 20px;
  position: relative;
  line-height: 1.4;
  border: 1px solid #fabe00;
}
.tasteRegister .rules header img {
  width: 90px;
  height: auto;
}
.tasteRegister .rules .rules-content {
  margin: 15px 0;
}
.tasteRegister .rules:after {
  content: '';
  display: block;
  height: 100%;
  width: 100%;
  position: absolute;
  left: 5px;
  bottom: 5px;
  border: 1px solid #fabe00;
}
.tasteRegister .form-container header {
  width: 35%;
  margin-bottom: 10px;
}
.tasteRegister .form-container .form-line {
  margin: 18px 0;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.tasteRegister .form-container .form-line .input-container {
  position: relative;
  margin-top: 5px;
}
.tasteRegister .form-container .form-line .radio {
  vertical-align: bottom;
}
.tasteRegister .form-container .form-line .input {
  width: 100%;
  height: 100%;
  padding: 5px;
  border-radius: 3px;
  border: 1px solid #fabe00;
}
.tasteRegister .form-container .form-line .input .is-danger {
  position: absolute;
  bottom: -14px;
  left: 0;
  font-size: 12px;
  color: #f00;
}


