
.nav {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  height: 40px;
  font-size: 10px;
  text-align: center;
  border-top: 1px solid #ccc;
  background: #fff;
}
.nav ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
}
.nav ul li {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  border-right: 1px solid #ccc;
}
.nav ul li .nav-item {
  display: block;
  height: 100%;
  padding-top: 4px;
}
.nav ul li .nav-item i {
  display: block;
  height: 18px;
  margin-bottom: 4px;
}
.nav ul li .nav-face i {
  background: url("./img/nav-face-off.png") no-repeat center;
  background-size: 18px auto;
}
.nav ul li .nav-mall i {
  background: url("./img/nav-mall-off.png") no-repeat center;
  background-size: 18px auto;
}
.nav ul li .nav-profile i {
  background: url("./img/nav-profile-off.png") no-repeat center;
  background-size: 18px auto;
}
.nav ul li .nav-face.on i {
  background: url("./img/nav-face-on.png") no-repeat center;
  background-size: 18px auto;
}
.nav ul li .nav-mall.on i {
  background: url("./img/nav-mall-on.png") no-repeat center;
  background-size: 18px auto;
}
.nav ul li .nav-profile.on i {
  background: url("./img/nav-profile-on.png") no-repeat center;
  background-size: 18px auto;
}
.nav ul li:last-child {
  border-right: 0;
}


