
@import '/static/vue-swipe.css';
.home {
  padding-bottom: 40px;
}
.home .head {
  padding: 20px;
}
.home .head h1 {
  margin-bottom: 10px;
  font-size: 16px;
}
.home .head .banner {
  width: 100%;
  height: 120px;
  margin-top: 8px;
  background: #ccc;
  overflow: hidden;
}
.home .head .banner img {
  width: 100%;
}
.home .taste-list li {
  margin-bottom: 10px;
  padding: 15px 20px;
  background: #fff;
}
.home .taste-list li .taste-face {
  margin-bottom: 10px;
  position: relative;
}
.home .taste-list li .taste-face .time-sign {
  position: absolute;
  top: -1px;
  left: 1.2em;
  color: #fff;
  height: 2.4em;
  line-height: 2.4;
  padding: 0 10px;
  background: #fabe00;
  font-size: 10px;
  text-align: center;
}
.home .taste-list li .taste-face .time-sign:before {
  content: '';
  display: block;
  width: 0;
  height: 0;
  position: absolute;
  left: -1.2em;
  border-top: 0.6em solid #fabe00;
  border-right: 0.6em solid #fabe00;
  border-left: 0.6em solid transparent;
  border-bottom: 0.6em solid transparent;
}
.home .taste-list li .taste-face .time-sign:after {
  content: '';
  display: block;
  width: 0;
  height: 0;
  position: absolute;
  left: -1.2em;
  bottom: 0;
  border-top: 0.6em solid transparent;
  border-right: 0.6em solid #fabe00;
  border-left: 0.6em solid transparent;
  border-bottom: 0.6em solid #fabe00;
}
.home .taste-list li .content h2 {
  font-size: 16px;
  margin-bottom: 6px;
}
.home .taste-list li .content p {
  line-height: 1.2;
  padding-bottom: 10px;
  border-bottom: 2px solid #eb191a;
}
.home .taste-list li img {
  width: 100%;
}
.my-swipe {
  height: 186px;
  color: #fff;
  font-size: 30px;
  text-align: center;
}
.my-swipe img {
  width: 100%;
}


