
.tasteDetails {
  padding: 12px 12px 50px 12px;
  background: #fff;
}
.tasteDetails h1 {
  font-size: 18px;
  line-height: 1.3;
}
.tasteDetails .info {
  margin: 8px 0 15px 0;
}
.tasteDetails .content {
  line-height: 1.4;
}
.tasteDetails .content img {
  max-width: 100%;
}
.tasteDetails .rules {
  margin: 25px 5px 25px 0;
  padding: 10px 10px 10px 20px;
  position: relative;
  line-height: 1.4;
  border: 1px solid #fabe00;
}
.tasteDetails .rules header img {
  width: 90px;
  height: auto;
}
.tasteDetails .rules .rules-content {
  margin: 15px 0;
}
.tasteDetails .rules:after {
  content: '';
  display: block;
  height: 100%;
  width: 100%;
  position: absolute;
  left: 5px;
  bottom: 5px;
  border: 1px solid #fabe00;
}
.tasteDetails .task span {
  font-size: 12px;
}
.tasteDetails .task ul li {
  margin-bottom: 10px;
}
.tasteDetails .task ul li a {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.tasteDetails .task .content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding-left: 8px;
  position: relative;
}
.tasteDetails .task .task-item-face {
  width: 38%;
}
.tasteDetails .task .desc {
  font-size: 12px;
  color: #999;
}
.tasteDetails .task .points {
  position: absolute;
  left: 8px;
  bottom: 0;
}
.tasteDetails .task .task-btn {
  padding: 4px 8px;
  position: absolute;
  right: 0;
  bottom: 0;
  font-size: 12px;
  color: #fff;
}
.tasteDetails .task h2,
.tasteDetails .reg-info h2 {
  margin-bottom: 15px;
  font-size: 18px;
  font-weight: bold;
}
.tasteDetails .reg-info {
  margin-top: 25px;
}
.tasteDetails .reg-info li {
  margin: 8px 0;
}


