
.uiDialog {
  position: fixed;
  top: -20px;
  left: -20px;
  right: -20px;
  bottom: -20px;
  text-align: center;
  background: rgba(0,0,0,0.6);
}
.uiDialog .dialog-wrapper {
  width: 76%;
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 4px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: #fff;
}
.uiDialog .dialog-wrapper .dialog-head {
  height: 40px;
  line-height: 40px;
  font-size: 16px;
}
.uiDialog .dialog-wrapper .dialog-body {
  min-height: 60px;
  padding: 0 10px;
  text-align: center;
  line-height: 1.2;
}
.uiDialog .dialog-wrapper .dialog-body .form-line {
  margin-bottom: 10px;
}
.uiDialog .dialog-wrapper .dialog-body .form-line label {
  display: block;
  margin-bottom: 4px;
}
.uiDialog .dialog-wrapper .dialog-body .form-line input {
  padding: 4px;
  border: 1px solid #ddd;
}
.uiDialog .dialog-wrapper .dialog-foot {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 40px;
  line-height: 40px;
  border-top: 1px solid #ddd;
}
.uiDialog .dialog-wrapper .dialog-foot .btn {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  border-right: 1px solid #ddd;
}
.uiDialog .dialog-wrapper .dialog-foot .btn:last-child {
  border-right: 0;
}


