
.profile {
  padding-bottom: 40px;
}
.profile .head {
  height: 115px;
  padding: 15px 0 0 0;
  margin-bottom: 10px;
  position: relative;
  background: url("profile-head-bg.jpg") no-repeat center top;
  background-size: 100% auto;
  text-align: center;
  color: #fff;
}
.profile .head .avatar {
  width: 50px;
  height: 50px;
  overflow: hidden;
  border-radius: 50%;
  margin: 0 auto;
}
.profile .head .nick {
  margin: 4px auto;
}
.profile .head .points {
  font-size: 12px;
}
.profile .head a {
  position: absolute;
  top: 15px;
  right: 15px;
}
.profile .module {
  padding: 10px 20px 15px 20px;
  margin-bottom: 10px;
  background: #fff;
}
.profile .module .module-head {
  padding: 10px 0;
  font-size: 16px;
}
.profile .module .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.profile .module .content .item {
  width: 48%;
  padding: 5px;
  background: #eee;
}
.profile .module .content .item .title {
  margin: 8px 0;
  font-weight: bold;
}
.profile .module .content .item .text {
  color: #666;
  line-height: 1.2;
}


