
.myReport {
  padding-bottom: 40px;
  background: #fff;
}
.myReport .question-list {
  padding: 15px;
}
.myReport .question-list .question-item {
  margin: 5px 0 10px 0;
}
.myReport .question-list .input-container,
.myReport .question-list .options {
  margin-top: 10px;
  padding-left: 16px;
}
.myReport .question-list .input {
  width: 80%;
  border-bottom: 1px solid #ccc;
}
.myReport .question-list .options .option {
  margin-right: 10px;
  display: inline-block;
}
.myReport .question-list .options .option input {
  vertical-align: middle;
}
.myReport .suggest {
  padding: 15px;
}
.myReport .suggest h3 {
  font-size: 16px;
}
.myReport .suggest textarea[name=suggest] {
  width: 100%;
  height: 6em;
  margin-top: 10px;
  resize: none;
}
.myReport .suggest .counts {
  margin-top: 10px;
  text-align: right;
}


