
.goodsDetails .head {
  background: #fff;
  text-align: center;
  padding-bottom: 10px;
}
.goodsDetails .head .carousel {
  height: 120px;
  margin: 10px auto;
}
.goodsDetails .head .carousel img {
  height: 100%;
}
.goodsDetails .points,
.goodsDetails .address,
.goodsDetails .description {
  margin-top: 10px;
  padding: 8px 10px;
  line-height: 1.2;
  background: #fff;
}
.goodsDetails .description h2 {
  font-size: 16px;
  margin-bottom: 10px;
}


