
.mall {
  padding-bottom: 40px;
}
.mall .welcome {
  background: #fff;
  padding: 12px;
  line-height: 1.2;
  text-align: center;
}
.mall > ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 10px 15px;
}
.mall > ul li {
  width: 48%;
  padding: 10px 0 30px 0;
  margin-bottom: 15px;
  position: relative;
  background: #fff;
}
.mall > ul li img {
  width: 100%;
}
.mall > ul li .title {
  margin-top: 8px;
  line-height: 1.2;
}
.mall > ul li .points,
.mall > ul li .cash-btn {
  position: absolute;
  bottom: 0;
  color: #fff;
  font-size: 12px;
}
.mall > ul li .points {
  left: 0;
  background: #fabe00;
  padding: 6px 5px;
}
.mall > ul li .cash-btn {
  right: 0;
  background: #eb191a;
  padding: 6px 1em;
}
.mall > ul li .points:before {
  content: '';
  display: block;
  width: 0;
  height: 0;
  position: absolute;
  right: -1.15em;
  top: 0;
  border-top: 0.6em solid #fabe00;
  border-right: 0.6em solid transparent;
  border-left: 0.6em solid #fabe00;
  border-bottom: 0.6em solid transparent;
}
.mall > ul li .points:after {
  content: '';
  display: block;
  width: 0;
  height: 0;
  position: absolute;
  right: -1.15em;
  bottom: 0;
  border-top: 0.6em solid transparent;
  border-right: 0.6em solid transparent;
  border-left: 0.6em solid #fabe00;
  border-bottom: 0.6em solid #fabe00;
}


