
.personal {
  h1: a;
}
.personal .head {
  padding: 20px;
  background: #fff;
}
.personal .head .avatar,
.personal .head .nick {
  display: inline-block;
  vertical-align: middle;
}
.personal .head .avatar {
  width: 50px;
  height: 50px;
  margin-right: 10px;
  border-radius: 25px;
  border: 1px solid #ccc;
  overflow: hidden;
}
.personal .info-list {
  margin-top: 10px;
  background: #fff;
}
.personal .info-list li {
  padding: 15px;
  border-bottom: 1px solid #ccc;
}
.personal .info-list li:last-child {
  border-bottom: 0;
}
.personal .phone-input-wrapper {
  text-align: center;
}
.personal .phone-input-wrapper input {
  border: 1px solid #ddd;
  margin-top: 10px;
  padding: 5px;
}


